<template>
  <!-- Navigation -->
  <nav
    class="navbar navbar-dark navbar-expand-md bg-dark justify-content-center"
  >
    <div class="container-fluid">
      <a href="/" class="navbar-brand">Passbild-Generator</a>
    </div>
  </nav>
  <!-- DragDropUpload -->
  <div @drop="dragFile" class="card">
    <div @dragover.prevent @drop.prevent>
      <div class="card-body bg-light">
        <input
          id="fileInput"
          class="justify-content form-control"
          type="file"
          multiple
          @change="uploadFile"
        />
      </div>

      <div class="dragFile text-center">
        <!-- <div v-if="File.length">
          <ul v-for="file in File" :key="file">
            <li>{{file.name}}</li>
          </ul>
        </div> -->
      </div>
    </div>
  </div>

  <div class="btn-group d-flex">
    <button @click="switch_through_image_last" class="btn btn-dark border">
      ⬅️ Last
    </button>
    <button @click="switch_through_image_next" class="btn btn-dark border">
      Next ➡️
    </button>
  </div>

  <!-- Cropper -->
  <div class="container">
    <div class="row">
      <div class="col-lg-6 col-md-12 col-sm-12">
        <cropper
          class="cropper"
          ref="cropper"
          :src="img"
          :stencil-props="{
            previewClass: 'stencil-overlay',
            aspectRatio: 3.5 / 4.5,
          }"
          @change="onChange"
          :debounce="false"
        />
      </div>
      <div class="col-md-12 col-lg-6 col-sm-12">
        <!-- Buttons -->
        <image-control-buttons
          @zoom_in_image="zoom_in_image"
          @zoom_out_image="zoom_out_image"
          @move_up_image="move_up_image"
          @move_down_image="move_down_image"
          @move_right_image="move_right_image"
          @move_left_image="move_left_image"
          @turn_right_image="turn_right_image"
          @turn_left_image="turn_left_image"
          @reset_image="reset_image"
        />
        <passportPreview ref="passportPreview" :imgFileName="this.image_file_names[this.counter]" />
      </div>
    </div>
  </div>
</template>
<script>
import { Cropper } from "vue-advanced-cropper";

import "vue-advanced-cropper/dist/style.css";

import passportPreview from "./passport-preview.vue";
import imageControlButtons from "./passport-image-control-buttons.vue";

export default {
  components: {
    Cropper,
    passportPreview,
    imageControlButtons,
  },
  data() {
    return {
      File: String,
      degree_counter: 0,
      counter: 0,
      uploaded_images: [],
      image_file_names:[],
      img: "https://images.unsplash.com/photo-1568602471122-7832951cc4c5?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1740&q=80",

      result: {
        coordinates: null,
        image: null,
      },
    };
  },
  methods: {
    uploadFile(e) {
      this.File = e.target.files;
      this.img = URL.createObjectURL(this.File[0]);
      for (let index = 0; index < this.File.length; index++) {
        console.log(this.File[index].name)

        this.uploaded_images.push(URL.createObjectURL(this.File[index]));
        this.image_file_names.push(this.File[index].name)
      }
    },
    dragFile(e) {
      this.File = e.dataTransfer.files;
      this.img = URL.createObjectURL(this.File[0]);
      for (let index = 0; index < this.File.length; index++) {
        this.uploaded_images.push(URL.createObjectURL(this.File[index]));
      }
    },
    switch_through_image_next() {
      // avoid bugs
      if (this.uploaded_images.length > 1) {
        this.counter += 1;
        this.img = this.uploaded_images[this.counter % this.uploaded_images.length];
      }
    },
    switch_through_image_last() {
      console.log(this.image_file_names);

      if (this.uploaded_images.length > 1) {
        this.counter -= 1;
        if (this.counter < 1) {
          this.counter = 0;
        }
        console.log(this.uploaded_images[this.counter % this.uploaded_images.length]);
        this.img = this.uploaded_images[this.counter % this.uploaded_images.length];
      }
    },
    zoom_out_image() {

      this.$refs.cropper.zoom(0.8);
    },
    zoom_in_image() {
      this.$refs.cropper.zoom(1.2);
    },
    move_up_image() {
      this.$refs.cropper.move(0, -10);
    },
    move_down_image() {
      this.$refs.cropper.move(0, 10);
    },
    move_right_image() {
      this.$refs.cropper.move(10, 0);
    },
    move_left_image() {
      this.$refs.cropper.move(-10, 0);
    },
    turn_right_image() {
      this.$refs.cropper.rotate(1);
      this.degree_counter += 1;
    },
    turn_left_image() {
      this.$refs.cropper.rotate(-1);
      this.degree_counter -= 1;
    },
    reset_image() {
      this.$refs.cropper.reset();
    },
    onChange({ coordinates, image }) {
      this.result = {
        coordinates,
        image,
      };
      // push data to the passportPreview component
      this.$refs.passportPreview.onChange({ coordinates, image });
    },
  },
};
</script>


<style>
.vue-advanced-cropper.cropper {
  min-height: 0px;
  width: 100%;
}
