<template>
  <div class="container">
    <div class="passportPreviewDiv">
      <div class="passportPreviewContainer" id="capture">
        <!-- Loop through 8 times -->
        <preview
          v-for="index in image_count_per_preview_container"
          :key="index"
          :width="x_wd_passport_preview"
          :height="y_wd_passport_preview"
          :image="result.image"
          :coordinates="result.coordinates"
          class="eachPassportImage"
        />
      </div>
    </div>

    <passport-buttons @download_preview_canvas="download_preview_canvas" />
  </div>
</template>
<script>
import { Preview } from "vue-advanced-cropper";
import html2canvas from "html2canvas";
import passportButtons from "./passport-control-buttons.vue";
export default {
  data() {
    var scale_wd_passport_preview = 2;
    var x_wd_standard = 35;
    var y_hd_standard = 45;
    var canvas_size = [150, 100];
    var gap_height = 10;
    var gap_width = 5;
    var preview_border_spacing_left = "10px";
    var preview_border_spacing_right = "10px";
    var preview_border_spacing_top = "10px";
    var preview_border_spacing_bottom = "10px";

    // Floor the values, to get standardized grid
    var image_count_horizontal = Math.floor(canvas_size[0] / x_wd_standard);
    var image_count_vertical = Math.floor(canvas_size[1] / y_hd_standard);
    return {
      image_count_per_preview_container: 8,

      x_wd_passport_preview: x_wd_standard * scale_wd_passport_preview,
      y_wd_passport_preview: y_hd_standard * scale_wd_passport_preview,
      image_count_horizontal,
      image_count_vertical,
      number_of_gaps_horizontal: image_count_horizontal - 1,
      number_of_gaps_vertical: image_count_vertical - 1,
      gap_height,
      gap_width,
      gap_height_px: String(gap_height) + "px",
      gap_width_px: String(gap_width) + "px",
      preview_border_spacing_top,
      preview_border_spacing_bottom,
      preview_border_spacing_left,
      preview_border_spacing_right,
      canvas_size_x: String(canvas_size[0])+"px",
      canvas_size_y: String(canvas_size[1])+"px",
      result: {
        coordinates: null,
        image: null,
      },
    };
  },
  props: ["imgFileName"],
  name: "passport-preview",
  components: {
    Preview,
    passportButtons,
  },

  methods: {
    onChange({ coordinates, image }) {
      console.log(this.image_count_vertical);
      this.result = {
        coordinates,
        image,
      };
    },

    download_preview_canvas() {
      html2canvas(document.querySelector("#capture"), {
        // allowTaint: true,
        useCORS: true,
        height:
          this.y_wd_passport_preview * this.image_count_vertical +
          this.gap_height * this.number_of_gaps_vertical +
          20 * this.number_of_gaps_vertical, // = 210
          // 
          // 
          // 
        width:
          this.x_wd_passport_preview * this.image_count_horizontal +
          this.gap_width * this.number_of_gaps_horizontal +
          5 * 4, //= 315
        // 315 / 210 = 1.5
        // Quality = 10 means witdh*10, height*10
        scale: 10,
      })
        .then((canvas) => {
          var passphoto_canvas = document.body.appendChild(canvas);
          passphoto_canvas;
          // hide Canvas
          passphoto_canvas.style.display = "none";
          canvas.toDataURL("image/png");

          var anchor = document.createElement("a");
          anchor.href = canvas.toDataURL("image/png");
          anchor.download = "PassportImage_" + this.imgFileName + ".png";
          anchor.click();
        })
        .then(() => {});
    },
  },
};
</script>
<style scoped>
.passportPreviewDiv {
  width: v-bind(canvas_size_x);
  height: v-bind(canvas_size_y);
  margin-top: 10px;
  margin-bottom: 100px;
}
.passportPreviewContainer {
  padding-left: v-bind(preview_border_spacing_left);
  padding-right: v-bind(preview_border_spacing_right);
  padding-top: v-bind(preview_border_spacing_top);
  padding-bottom: v-bind(preview_border_spacing_bottom);

  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  /* gap: v-bind(gap_height_px) v-bind(gap_width_px); */
  gap: v-bind(gap_height_px) v-bind(gap_width_px);

  grid-template-areas:
    ". . . ."
    ". . . .";
}
/* .eachPassportImage{
} */
</style>